  <div class="exportRecord">
    <div class="main_top">
       <el-row>
           <el-col :span="2"><div class="grid-content bg-purple right">店铺名称：</div></el-col>
        <el-col :span="22">
        
              <el-checkbox-group v-model="checkboxshop" size="mini" >
              <el-checkbox-button  :label="0"   @change="changeshop(0)" :key="0">全部</el-checkbox-button>
              <el-checkbox-button v-for="Shop in storeList" :label="Shop.id"  @change="changeshop(Shop.id)" :key="Shop.id">{{Shop.store_name}}</el-checkbox-button>
            </el-checkbox-group>
          
          </el-col>
      </el-row>
       <el-row>
        <el-col :span="2"><div class="grid-content bg-purple right">是否下载：</div></el-col>
        <el-col :span="22"> 
            <el-radio-group v-model="selectValueExport"  size="mini" @change="changeExportState">
              <el-radio-button  v-for="item in exportBtnList" :key="item.id" :label="item.id" :value="item.id" >{{item.name}}</el-radio-button>
            </el-radio-group>
          
          </el-col>
       </el-row>
      
      <el-row>
        <el-col :span="15">
          <span>下载时间：</span>
          
          <el-date-picker
            type="datetime"
            placeholder="开始日期"
            v-model="DownloadStartTime" value-format="yyyy-MM-dd hh:mm:ss" 
            style="display: inline-block;width: 33%!important;"
          ></el-date-picker>
          <span style="display: inline-block;width: 3%; padding: 0!important;amrgin: 0!important;text-align:center;">~</span>
          <el-date-picker
            type="datetime"
            placeholder="结束日期"
            v-model="DownloadEndTime" value-format="yyyy-MM-dd hh:mm:ss" 
            style="display: inline-block;width: 33%!important;"
          ></el-date-picker>
        </el-col>
      </el-row>
       <el-row>
        <el-col :span="15">
          <span>汇出时间：</span>
          <el-date-picker
            type="datetime"
            placeholder="开始日期"
            v-model="ExportStartTime" value-format="yyyy-MM-dd hh:mm:ss" 
            style="display: inline-block;width: 33%!important;"
          ></el-date-picker> 
          <span style="display: inline-block;width: 3%; padding: 0!important;amrgin: 0!important;text-align:center;">~</span>
          <el-date-picker
            type="datetime"
            placeholder="结束日期"
            v-model="ExportEndTime" value-format="yyyy-MM-dd hh:mm:ss" 
            style="display: inline-block;width: 33%!important;"
          ></el-date-picker>
           <el-button type="primary" size="small"  @click="searchrecord">搜索</el-button>
           <el-button  size="small"  @click="resetrecord" >重置</el-button>
        </el-col>
        <el-col :span="5" style="text-align:left;">
         
        </el-col>
      </el-row>
      <el-row style="color:#f7898a;">
        请在图片同步状态为“同步成功”后再下载上传亚马逊（因系统容量原因，图片保存10天，过期请重新汇出）；
      </el-row>
      <el-row style="color:#f7898a;">
        使用Excel编辑文件时，请将“external_product_id”对应列的单元格格式转换成无小数的数值格式，否则UPC会自动转为科学计数法，导致上传到亚马逊报错。
      </el-row>
      <el-row style="color:#f7898a;">
      <div class="tableTip">汇出记录90天后会自动删除，避免占用服务器空间（不影响已汇出的定制产品）</div>
      </el-row>
    </div>
    <div class="main_bottom">
      
       <div class="right">
        <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[50, 100, 200]"
              :page-size="100"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
      </div>
        
      <div style="width: 100%;margin-bottom: 50px;padding: 0 10px !important;box-sizing: border-box;" >
      <el-table :data="tableData" stripe style="width: 100%;" class="exportRecordTable" v-loading='exportRecordLoading' align='center'>
        <el-table-column prop="id" label="编号" width="70" align='center' ></el-table-column>
        <el-table-column prop="create_at" label="汇出时间" min-width="100" align='center' show-overflow-tooltip></el-table-column>
        <el-table-column prop="template_name" label="汇出模板" min-width="100" align='center'></el-table-column>
