        <el-table-column prop="sku_num" label="汇出SKU数" width="110" align='center'></el-table-column>

        <!-- <el-table-column prop="" label="是否使用商品编码" min-width="100" align='center'></el-table-column> -->
        <el-table-column prop="store_name_alias" label="所属店铺" width="100" align='center'></el-table-column>
        <el-table-column prop="create_status" label="汇出状态" width="110" align='center'>
           <template slot-scope="scope">
             <el-button size="mini"  v-if="scope.row.create_status == 0"  >处理中</el-button>
            <el-button size="mini"  v-if="scope.row.create_status == 1" >成功</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="download_time" label="下载时间" min-width="100" align='center' show-overflow-tooltip></el-table-column>
        <el-table-column prop="" label="操作" min-width="120" align='center' >
          <template slot-scope="scope">
             <el-button size="mini"  v-if="scope.row.create_status == 0"  >正在后台处理中 请稍后再来下载！</el-button>
            <el-button size="mini"  v-if="scope.row.create_status == 1" @click="download(scope.row.path,scope.row.id)">下载(xlsx)</el-button>
          </template>
        </el-table-column>
      </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
       selectValueExport:0,//是否汇出
      checkboxshop:[0],
       storeList:[],//店铺列表
      exportRecordLoading: false,  //汇出记录表格加载中
      tableData: [], //汇出记录列表
      shop: '全部', //所属店铺
      isDownload: '全部',  //是否下载
      DownloadStartTime: '',
      DownloadEndTime:'',
      ExportStartTime: '',
      ExportEndTime: '',
      shopList: [],
     exportBtnList:[
          {
            name: '全部',
            id: 0
          },{
            name: '是',
            id: 1
          },{
            name: '否',
            id: 2
          },
      ],
      total: 0,
      pageSize: 0,
      currentPage: 1,
      limit: 50,// 数据条数
    };
  },
  methods: {
    searchrecord(){
      this.getExportRecordList();
    },
    resetrecord(){
       this.DownloadStartTime="";
        this.DownloadEndTime="";
        this.ExportStartTime="";
         this.ExportEndTime="";
        this.getExportRecordList();
    },
     changeExportState(val) {
      this.selectValueExport=val;
      this.getExportRecordList();
    },
     changeshop(val){
       if(val==0)
       {
         this.checkboxshop=[0];
       }
       else{
         if(this.checkboxshop.indexOf(0)!=-1){
            this.checkboxshop.splice(this.checkboxshop.indexOf(0),1);
         }
       }
       if(this.checkboxshop.length==0)
       {
          this.checkboxshop=[0];
       }
      //  console.log(this.checkboxshop);
      //  this.$store.state.shopManagementCate.store_id=this.checkboxshop;
      this.getExportRecordList();
    },
     
    // 选择是否下载
    isDownLoadFun(value) {
      this.isDownload = value
    },
    //点击下载
    async download(path,id) {
         var u_id=sessionStorage.getItem('userId');
      // window.location.href= 'http://192.168.2.158:8080/v1/downloadTxt?url='+path
      window.location.href= 'http://api.321.design/v1/downloadTxt?url='+path+"&id="+id+"&u_id="+u_id;
      // axios.defaults.baseURL
      //  this.postRequest(`/v1/downloadTxt?url=${path}`)
      //   .then(res => {
      //   })
      // // let res = await this.api.downloadTxt({url: path})
      // window.location.href = path
    },
    handleSizeChange(val) {
      this.limit = val
      this.getExportRecordList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getExportRecordList();
    },
     // 获取店铺列表
    async getStoreList() {
      const res = await this.api.stoLists()
      if(res.data.code == 200) {
        this.storeList = res.data.data
      //  this.customTemplateForm.store_name = this.storeList[0].id
      }
    },
    // 获取汇出记录列表
    async getExportRecordList() {
      this.exportRecordLoading = true
      let u_id = sessionStorage.getItem('userId')
      let res = await this.api.exportRecord({
        page: this.currentPage,
        limit: this.limit,
        store_id:this.checkboxshop,
        selectValueExport:this.selectValueExport,
        DownloadStartTime:this.DownloadStartTime,
        DownloadEndTime:this.DownloadEndTime,
        ExportStartTime:this.ExportStartTime,
        ExportEndTime:this.ExportEndTime
      })
      if (res.data.code == "200") {
        this.tableData = res.data.data.data;
        this.total = res.data.data.total;
        this.pageSize = res.data.data.per_page;
        this.exportRecordLoading = false
      }else {
        this.errorMess('获取汇出列表失败')
      }
    },
  },
  created() {
    this.getExportRecordList();
    this.getStoreList();
  }
};
</script>
